'use client'
import React, { useState } from 'react';
import Button from '@mui/material/Button';
import SendIcon from '@mui/icons-material/Send';


export default function Home() {
  const [showRegisterModal, setShowRegisterModal] = useState(false);
  const [loginData, setLoginData] = useState({ username: '', password: '' });
  const [registerData, setRegisterData] = useState({
    username: '',
    password: '',
    email: '',
    role: 'usuario'
  });

  const handleRegisterChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setRegisterData({ ...registerData, [e.target.name]: e.target.value });
  };

  const handleLoginChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setLoginData({ ...loginData, [e.target.name]: e.target.value });
  };

  const handleRegisterSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const res = await fetch('http://localhost:8000/users', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(registerData)
    });
    const data = await res.json();
    console.log('Registro:', data);
    alert("Usuario registrado correctamente");
    setShowRegisterModal(false);
  };

  const handleLoginSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const res = await fetch('http://localhost:8080/login', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(loginData)
    });
    const data = await res.json();
    if (res.ok) {
      alert("Login exitoso");
    } else {
      alert("Credenciales incorrectas");
    }
  };

  return (
    <div style={{ padding: 20 }}>
      <h1>Iniciar Sesión</h1>
      <form>
        <input type="text" name="username" placeholder="Usuario" onChange={handleLoginChange} /><br />
        <input type="password" name="password" placeholder="Contraseña" onChange={handleLoginChange} /><br />
         <Button variant="contained" endIcon={<SendIcon />} onClick={handleLoginSubmit}>
        Ingresar
      </Button>
      </form>

      <button onClick={() => setShowRegisterModal(true)} style={{ marginTop: 20 }}>
        Registrarse
      </button>

      {/* Modal de Registro */}
      {showRegisterModal && (
        <div style={modalOverlayStyle}>
          <div style={modalContentStyle}>
            <h2>Registro de Usuario</h2>
            <form onSubmit={handleRegisterSubmit}>
              <input type="text" name="username" placeholder="Usuario" onChange={handleRegisterChange} /><br />
              <input type="email" name="email" placeholder="Correo" onChange={handleRegisterChange} /><br />
              <input type="password" name="password" placeholder="Contraseña" onChange={handleRegisterChange} /><br />
              <button type="submit">Registrar</button>
              <button type="button" onClick={() => setShowRegisterModal(false)} style={{ marginLeft: 10 }}>
                Cancelar
              </button>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}

const modalOverlayStyle: React.CSSProperties = {
  position: 'fixed',
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  backgroundColor: 'rgba(220, 202, 248, 0.74)',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  zIndex: 1000,
};

const modalContentStyle: React.CSSProperties = {
  backgroundColor: 'rgb(230, 220, 250)', 
  color: 'rgb(0, 0, 0)',                         
  padding: '20px',
  borderRadius: '12px',
  minWidth: '320px',
  boxShadow: '0 0 15px rgba(50, 9, 163, 0.81)', 
};
